<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Asignacin de Vehculos
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="12"
      >
        <form @submit.prevent="guardarVehiculo()">
          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-select
                v-model="vehicles.brand"
                label="Marca"
                :items="marcas"
                outlined
                prepend-icon="mdi-tag"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.cod_tdp"
                label="COD TDP"
                outlined
                prepend-icon="mdi-barcode"
                required
                disabled
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.serie_tdp"
                label="SERIE TDP"
                outlined
                prepend-icon="mdi-barcode-scan"
                required
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-select
                v-model="vehicles.category"
                label="Categoria"
                :items="categoria"
                outlined
                prepend-icon="mdi-group"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="8"
            >
              <v-text-field
                v-model="vehicles.model"
                label="Modelo"
                outlined
                prepend-icon="mdi-car"
                required
                disabled
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.colour"
                label="Color"
                outlined
                prepend-icon="mdi-palette"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.manufacturing_year"
                label="Ao Fabricacin"
                outlined
                prepend-icon="mdi-calendar-range"
                type="number"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.model_year"
                label="Ao Modelo"
                outlined
                prepend-icon="mdi-calendar-range"
                type="number"
                required
                disabled
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.location"
                label="Ubicacion"
                outlined
                prepend-icon="mdi-crosshairs-gps"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.brand_office"
                label="Sucursal"
                outlined
                prepend-icon="mdi-map-marker"
                required
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.price"
                label="Precio"
                outlined
                prepend-icon="mdi-cash"
                prefix="$"
                required
                disabled
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <v-select
                v-model="vehicles.status"
                label="Estado"
                :items="['Libre', 'Asignado','Por Conseguir']"
                outlined
                prepend-icon="mdi-group"
                required
              />
            </v-col>
          </v-row>

          <v-card-actions>
            <v-btn
              to="/adv/vehiculos"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Actualizar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'

  const urlVehicle = 'https://sistema-adv.herokuapp.com/api/vehicles/'

  export default {
    name: 'EditarVehiculo',
    data () {
      return {
        vehicles: {
          brand: '',
          cod_tdp: '',
          serie_tdp: '',
          category: '',
          model: '',
          colour: '',
          manufacturing_year: '',
          model_year: '',
          brand_office: '',
          location: '',
          price: '',
          status: '',

        },
        marcas: ['Toyota', 'Hino'],
        categoria: ['Sedan', 'Hatchback', 'SUV', 'PickUp', 'Coupe'],
      }
    },
    mounted () {
      this.activarEdicion()
    },
    methods: {
      activarEdicion () {
        this.id = this.$route.params.id
        axios.get(urlVehicle + this.id).then(res => {
          this.vehicles = res.data
        }).catch(e => { console.log(e) })
      },
      guardarVehiculo () {
        const router = this.$router
        const params = this.vehicles

        axios.put(urlVehicle + this.id, params).then(res => {
          router.push('/adv/vehiculos')
        })
      },
    },
  }
</script>
